img {
    width: 100%;
}

.landing {
    position: relative;
    background: url("./img/showcase.jpg") no-repeat;
    background-size: cover;
    background-position: center;
    height: 88vh;
    margin-bottom: -50px;
}

.auth-pages {
    height: 87.4vh;
    background: #ffe4d3;
    color: #5a5a7a;
    /* padding-top: 30px; */
}

.action-pages {
    min-height: 88vh;
    background: #fcfbf7;
    color: #5a5a7a;
    /* padding-top: 30px; */
}

.create-post-section .ck-editor__editable_inline {
    height: 250px;
    background: #ffe4d3;
}

.ck.ck-editor__main>.ck-editor__editable {
    background-color: #fcfbf7 !important;
}

.ck.ck-editor__editable_inline {
    border: none;
}

.navbar {
    padding: 0 !important;
}

.landing-inner {
    padding-top: 80px;
}

.dark-overlay {
    background-color: rgba(0, 0, 0, 0.3);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.card-form {
    opacity: 0.9;
}

.latest-profiles-img {
    width: 40px;
    height: 40px;
}

.form-control::placeholder {
    color: #bbb !important;
}

/* NAVBAR */
.navbar-cust {
    background: #5a5a7a !important;
    color: #fec7a7;
}

.navbar-cust a {
    color: #ffe4d3 !important;
}

footer {
    /* position: fixed; */
    bottom: 0;
    width: 100%;
}

.create-post-section {
    padding: 0;
    width: 98.5vw;
    margin-left: -15px !important;
}

.create-post-left-section {
    background: #ffe4d3;
    min-height: 25vh;
    padding: 0;
}

.create-post-right-section {
    background: #fff1ea;
    min-height: 25vh;
    padding: 0;
}

.card {
    border-radius: 6px;
    border: 5px solid;
    margin: 7px !important;
}

.card-deck {
    background: #f6f3e8;
}

.custom-card {
    background: #fff1ea;
    color: #ddd2d3;
    padding: 0;
}

.custom-card-date {
    background: #585a79;
    color: #ddd2d3;
    padding: 8px;
    margin-bottom: 0;
}

.custom-card-header {
    background: #ffe4d3;
    color: #585a79;
    padding: 8px;
    margin-bottom: 0;
}

.custom-card-content {
    background: #fff1ea;
    color: #9d96a2;
    margin-bottom: 0;
}

.custom-card-tags {
    padding: 15px 8px;
}

.pad15 {
    padding: 15px !important;
}

.save-btn {
    background: #5A5A7A;
    color: #ffe4d3 !important;
    margin: 5px;
    width: 99%;
    float: right;
    font-weight: bold;
    letter-spacing: 2px;
}

